html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background: lightcyan;
  color: lightcoral;
  min-height: 100vh;
  font-family: "Montserrat", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.simple-editor {
  width: 100%;
  max-width: 400px;
  height: 500px;
  background: #fff;
  border: 4px solid lightgray;
  border-top-right-radius: 50px;
  border-bottom-left-radius: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 4px 4px 4px 4px lightgray; }
  .simple-editor__header {
    padding-bottom: 20px;
    font-size: 35px; }

.form {
  width: 100%;
  max-width: 300px;
  z-index: 5; }
  .form__wrapper {
    padding-bottom: 10px; }
  .form__title--label {
    font-weight: bold; }
  .form__title--input {
    border: 2px solid lightgray;
    padding: 10px;
    margin-top: 10px;
    background: none;
    width: 100%; }
  .form__content--label {
    font-weight: bold; }
  .form__content--input {
    border: 2px solid lightgray;
    padding: 10px;
    margin-top: 10px;
    width: 100%;
    height: 130px; }
  .form__buttons {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 20px; }
    .form__buttons--save {
      margin-right: 20px;
      background: #fff;
      border: 3px solid lightcoral;
      color: lightcoral;
      padding: 8px 25px;
      position: relative;
      transition: background 0.6s, color 0.6s; }
      .form__buttons--save::before {
        content: "";
        position: absolute;
        display: block;
        background: lightcoral;
        width: 100%;
        height: 100%;
        top: 7px;
        left: 7px;
        z-index: -1; }
      .form__buttons--save:hover {
        background: lightcoral;
        color: #fff; }
    .form__buttons--load {
      background: #fff;
      border: 3px solid lightseagreen;
      color: lightseagreen;
      padding: 8px 25px;
      position: relative;
      transition: background 0.6s, color 0.6s; }
      .form__buttons--load::before {
        content: "";
        position: absolute;
        display: block;
        background: lightseagreen;
        width: 100%;
        height: 100%;
        top: 7px;
        left: 7px;
        z-index: -1; }
      .form__buttons--load:hover {
        background: lightseagreen;
        color: #fff; }

@media screen and (max-width: 640px) {
  .simple-editor {
    border: none;
    background: none;
    box-shadow: none;
    max-width: 350px; }
    .simple-editor__header {
      font-size: 25px; }
  .form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    .form__title--input {
      border: 1px solid lightcoral;
      background: lightcyan;
      width: 250px; }
    .form__content--input {
      border: 1px solid lightcoral;
      background: lightcyan;
      width: 250px; }
    .form__buttons {
      margin-right: 70px; } }
